body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC",
    "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: rgba(0, 0, 0, 0.65);
}

.search{margin:5rem 0 0 0;width:23rem}.search__header{margin:1rem;font-size:1.3rem}.search__area{display:flex;flex-direction:row;align-items:center;justify-content:flex-start;flex-wrap:wrap;min-height:2.3rem;margin-top:0.5rem;border:0.05rem solid #c0c0c0;border-radius:0.5rem}.search__area:hover,.search__area:focus{border-color:#40a9ff}.search__item{margin:0.2rem;padding:0.3rem;color:rgba(0,0,0,0.65);background-color:#fafafa;border:1px solid #e8e8e8;border-radius:0.125rem;display:flex;font-size:0.85rem}.search__field{margin:0.2rem;padding:0.3rem;border:none;outline:0;width:2rem;flex-grow:3;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";font-size:0.85rem;color:rgba(0,0,0,0.65)}.search__close{margin-left:0.1rem;opacity:0.3;cursor:pointer}.search__close:hover{opacity:1}.search__cross{text-align:center;text-transform:none;vertical-align:-0.125em}

.list{display:flex;flex-direction:column;position:absolute;background-color:#fff;width:23rem;max-height:10rem;box-shadow:0 2px 8px rgba(0,0,0,0.15);border-radius:0.25rem;border-top:none;overflow:auto;margin:0;padding:0;z-index:10}.list__item{display:flex;align-items:center;padding:0.2rem;font-size:0.85rem;line-height:1.25rem;cursor:pointer;outline:0}.list__item:hover,.list__item:focus{background-color:#e6f7ff}.list__text{padding-left:1rem}

.page__main {
  margin-left: 10rem;
}

